.bankets {
  margin: 3rem 8rem;
}

.bankets_grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 8rem;
}

.image img {
  width: 100%;
  min-height: 70vh;
  max-height: 80vh;
  object-fit: cover;
  border-radius: 25px;
  transition: all 0.1s ease-in;
}

.image img:hover {
  transform: rotate(2deg);
  box-shadow: 0px 7px 5px gray;
}

