.about_bankets {
  display: flex;
  flex-direction: column;
}

/* Lielais virsrakts */
.bankets_title {
  font-size: 42px;
  font-weight: 600;
  border-bottom: 2px solid #3abadf;
  padding-bottom: 1rem;
}

/* Banketa Zilais apaksvirsraksts */
.bankets_subtitle {
  font-size: 36px;
  color: #3abadf;
  font-weight: 600;
  padding: 2rem 0;
}

/* Banketa uzskaites punkti */
.bankets_items {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
}

/* Katrs no banketa uzskaites punktiem */
.bankets_item {
  display: grid;
  grid-template-columns: 15% 85%;
}

.bankets_item .fas {
  color: #3abadf;
  font-size: 32px;
}

.bankets_text {
  font-size: 18px;
  font-weight: 500;
}

.bankets_text a {
  text-decoration: underline;
  color: #3abadf;
  font-weight: 600;
}

@media only screen and (max-width: 950px) {
  .bankets {
    margin: 2rem;
    margin-top: 4rem;
  }

  .bankets_grid {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
  }

  .image img {
    min-height: 0;
    height: 30vh;
  }

  .bankets_title {
    font-size: 32px;
  }

  .bankets_subtitle {
    font-size: 28px;
  }

  .bankets_item {
    padding-bottom: 2rem;
    grid-template-columns: 20% 80%;
  }

  .banekts_item .fas {
    padding: 6px;
