/* Reserve */

.reserve {
  min-height: 100vh;
  padding: 2rem;
  margin: 3rem 15rem;
}

.reserve_title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.reserve form {
  padding-top: 1rem;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.reserve h1 {
  text-align: center;
  margin: 2rem 10rem;
  padding-bottom: 2rem;
  font-size: 42px;
  width: 50%;
  border-bottom: 2px solid #3abadf;
}

.reserve form label {
  padding-bottom: 5px;
  font-weight: 600;
  padding-left: 1rem;
}

.reserve form input {
  border: none;
  border-bottom: 1px solid white;
  font-size: 18px;
  padding: 5px 0;
  margin-bottom: 1rem;
  padding-left: 1rem;
  transition: all 0.5s ease-in-out;
  width: 100%;
}

.reserve form input:focus {
  outline: none;
  border-bottom: 1px solid #3abadf;
  width: 100%;
}

.reserve button {
  cursor: pointer;
  border: none;
  color: white;
  width: 40%;
  font-size: 24px;
  padding: 1rem;
  font-weight: 600;
  background: #3abadf;
  border-radius: 25px;
  transition: all 0.2s ease-in;
  align-self: center;
}

.reserve button:focus {
  outline: none;
}

.reserve button:hover {
  background: whitesmoke;
  color: #3abadf;
}

.phone_number {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem 0;
}

.phone_plus {
  font-size: 18px;
}

#phone {
  margin-bottom: 0;
  margin-top: 0;
}

@media only screen and (max-width: 950px) {
  .reserve form input {
    margin: 0.5rem 0rem;
    border: 1px solid #3abadf;
    border-radius: 25px;
    padding: 0.5rem 1rem;
    font-size: 16px;
  }

  .reserve form label {
    margin-top: 0.5rem;
  }

  .reserve {
    margin: 0;
    margin-bottom: -1rem;
  }

  .reserve h1 {
    width: 100%;
  }

  .reserve button {
    width: 100%;
    padding: 1rem;
    margin-top: 2rem;
    font-size: 20px;
  }
}

.image-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 0 0px;
  grid-auto-rows: 10px;
